import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ConnectiontoAPI } from '../../../ConnectiontoAPI';
// import { Observable } from 'rxjs/Observable';
// import { map } from 'rxjs/operators';
import { Post } from '../../models/post.model'

@Injectable({
  providedIn: 'root'
})
export class PostApartmentService {

  constructor(private http: HttpClient) { }

  getPosts() {
   return this.http.get(`${ConnectiontoAPI.SERVER_BASE_URL}/posts`);
 }

 // getPostByPlace(queryTitle: string): Observable<Post[]> {
 //    return this.http.get(`${this.API_PATH}/posts?q=${queryTitle}`)
 //      .map(res => res.json().items || []);
 //  }

 getPostById(id) {
   return this.http.get(`${ConnectiontoAPI.SERVER_BASE_URL}/posts/${id}`);
 }

 addPost(title, description, state, city, status) {
    const post = {
      title: title,
      description: description,
      state: state,
      city: city,
      status: status
    };
    return this.http.post(`${ConnectiontoAPI.SERVER_BASE_URL}/posts/add`, post);
  }

  updatePost(id,title, description, state, city, street, status) {
    const post = {
      title: title,
      description: description,
      state: state,
      city: city,
      street: street,
      status: status
    };
    return this.http.post(`${ConnectiontoAPI.SERVER_BASE_URL}/posts/update/${id}`, post);
  }

  deletePost(id) {
    return this.http.get(`${ConnectiontoAPI.SERVER_BASE_URL}/posts/delete/${id}`);
  }
}
